import React from 'react'
import { Button, Input } from 'antd'
import Layout from '../../Layout/Layout'
import { connect } from '../../sw-store'
class Todos extends React.Component {
	constructor() {
		super()
		this.state = {
			task: '',
		}
	}
	render() {
		console.log(this.props)
		const { tasks, setTasks } = this.props
		const todoItem = tasks.map((v, index) => <div key={index}>{v}</div>)
		return (
			<Layout>
				<h1>这是一个Todos 的 Demo</h1>
				{todoItem}
				<Input placeholder="input task" />
			</Layout>
		)
	}
}
export default connect(
	states => ({
		tasks: states.todo.tasks,
	}),
	dispatchers => ({
		setTasks: dispatchers.todo.setTasks,
	})
)(Todos)
